@import "../../variables.scss";

.swiperSlide {
    // background-image: url(../../../img/Swiper/autoSlide1.jpg);

    &:nth-of-type(1) {
        background-image: url(../../../img/Swiper/autoSlide1.jpg);
    }
    &:nth-of-type(2) {
        background-image: url(../../../img/Swiper/autoSlide2.jpg);
    }
    &:nth-of-type(3) {
        background-image: url(../../../img/Swiper/autoSlide3.jpg);
    }
    &:nth-of-type(4) {
        background-image: url(../../../img/Swiper/autoSlide4.jpg);
    }
    &:nth-of-type(5) {
        background-image: url(../../../img/Swiper/autoSlide5.jpg);
    }
    &:nth-of-type(6) {
        background-image: url(../../../img/Swiper/autoSlide1.jpg);
    }
    &:nth-of-type(7) {
        background-image: url(../../../img/Swiper/autoSlide2.jpg);
    }
    &:nth-of-type(8) {
        background-image: url(../../../img/Swiper/autoSlide3.jpg);
    }
    &:nth-of-type(9) {
        background-image: url(../../../img/Swiper/autoSlide4.jpg);
    }
    &:nth-of-type(10) {
        background-image: url(../../../img/Swiper/autoSlide5.jpg);
    }
    width: 20em;
    height: 20em !important;
    @media (max-width: 992px) {
        height: 25em !important;
    }
    background-size: cover;
}
.swiperContainer {
    padding-bottom: 1.875em;
    div:nth-of-type(1) {
        @media (min-width: 769px) {
            height: 0.625em;
            background-color: $headerText;
            border-radius: 0;
        }
        div:nth-child(1) {
            @media (min-width: 769px) {
                background-color: $headerTextSecondary;
            }
        }
    }
}
